@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@600;700&display=swap");


  
ul {
  position: relative;
  background: #fff;
  display: flex;
  justify-content: center;
  padding: 10px 20px;
  border-radius: 50px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

ul li {
  list-style: none;
  line-height: 50px;
  margin: 0 5px;
}

ul li.pageNumber {
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
}

ul li a {
  display: block;
  text-decoration: none;
  color: #383838;
  font-weight: 600;
  border-radius: 50%;
}

ul li.pageNumber:hover a {
  background: #383838;
  color: #fff;
}

ul li:first-child {
  margin-right: 30px;
  font-weight: 700;
  font-size: 20px;
}

ul li:last-child {
  margin-left: 30px;
  font-weight: 700;
  font-size: 20px;
}
.active{
  background: #383838;
  color: white;
}